<template>
  <div class="login">
    <div class="login-wrap">
    <el-form
        :model="loginForm"
        status-icon
        :rules="rules"
        ref="loginForm"
        label-width="100px"
        class="demo-ruleForm"
    >
      <h3>Login</h3>
      <el-form-item label="Username" prop="userName" required>
        <el-input prefix-icon="el-icon-user" v-model.number="loginForm.userName"></el-input>
      </el-form-item>
      <el-form-item label="Password" prop="password" required>
        <el-input
            type="password"
            v-model="loginForm.password"
            autocomplete="off"
            prefix-icon="el-icon-lock"
        ></el-input>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" plain @click="login('loginForm')">Submit</el-button>
      </el-form-item>
      <router-link to="/register">Register</router-link>
    </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    var checkUserId = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('Please enter user name!'))
      }
      if (Number.isInteger(value)) {
        callback(new Error('Please enter characters!'))
      } else {
        callback()
      }
    }

    return {
      loginForm: {
        userName: '',
        password: '',
      },
      rules: {
        password: [{ message: 'Please enter password!', trigger: 'blur' },
          { min: 6, max: 20, message: 'Password should be 6 ï½ž 20 characters', trigger: 'blur' }],
        userName: [{ validator: checkUserId, trigger: 'blur' }],
      },
    }
  },
  methods: {
    login(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$axios.post('/login', {
            userName: this.loginForm.userName,
            password: this.loginForm.password
          })
              .then((response) => {
                if (response.data.msg==="SUCCESS") {
                  this.$message({
                    showClose: true,
                    message: 'Login successfully! You can view your SmartHome now',
                    type: 'success'
                  })
                  this.$router.push('/center')
                }else if(response.data.msg==="WRONG_PASSWORD"){
                  this.$message({
                    showClose: true,
                    message:'Wrong password',
                    type:'error'
                  })
                }else if(response.data.msg==="NO_SUCH_USER"){
                  this.$message({
                    showClose: true,
                    message:'No such user',
                    type:'error'
                  })
                  // alert(JSON.stringify(response))
                }
              })
              .catch(function (error) {
                console.log(error)
              })
        } else {
          return false
        }
      });
    },
  }
}
</script>

